.formControl,
.formControl div,
.formControl span {
  margin: 0;
  padding: 0;
}

.formControl.error textarea,
.formControl.error input {
  border: solid 2px red;
}

.formControl.error span {
  color: red;
}

.formSummaryError {
  border: solid red 1px;
  margin: 10px;
  padding: 10px;
  color: red;
}
